.market {
    width: 360px;
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color:  #fff;
    top: 65px;
    right: 165px;
    border-radius: 4px;
    justify-content: space-between;
}
.market_img {
    width: 100px;
    height: 60px;
}
.market_body {
    display: flex;
    flex-direction: row;
    padding: 12px;
    position: relative;
}
.market_btn {
    display: flex;
    align-items: center;
}
.market_footer{ 
    margin-top: 40px;
    margin-left: 50px;
}
.markettext2 {
    width: 170px;
    font-weight: 500;
    margin: 0px 0px 0px 0px;
    font-size: 18px;
}
.markettext {
    margin: 0px 0px 0px 0px;
    font-size: 15px;
}
.marketitem {
    width: 100px;
    margin-left: 16px;
}
.marketbtnincrease {
    width: 25px;
    height: 25px;
    background-color: rgb(232, 239, 246);
    border: none;
}
.marketbtnincrease:hover {
    cursor: pointer;
    background-color: rgb(232, 239, 246);
}
.time {
    display:flex;
    justify-content:center;
    align-items: center;
    width: 24px;
    height: 24px;
    position: absolute;
    top: 10px;
    right: 10px;
}
.time:hover {
    border-radius: 50%;
    cursor: pointer;
    background-color: #efefef;
}
.markettotal {
    padding: 8px;
    width: 100%;
    display: flex;
}
.total {
    display: flex;
    justify-content: space-between;
}
.totalprice {
    margin-left: 180px;
    color: #00509d;
    font-size: 18px;
    font-weight: 500;
}
.hr {
    width: 100%;
    height: 1px;
    border: 1px solid #ccc;
}
.tong {
    font-weight: 500;
    font-size: 18px;
}